.offer-navigation__link {
  text-decoration: none;
  color: @black;
  font-weight: 700;

  &:hover,
  &:focus {
    color: @color-menu-hover;
  }

  &:active {
    opacity: 0.3;
  }
}

.offer-navigation {
  margin: 0;
  padding-left: 0;
  display: none;

  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    order: 3;
    width: 51%;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1;
  }
}

.offer-navigation__item {
  padding-top: 25px;
  padding-bottom: 25px;
  border-top: 1px solid @gray;
  list-style: none;

  &:not(:last-child) {
    @media (min-width: @desktop-width) {
      margin-right: 60px;
    }
  }

  @media (min-width: @tablet-width) {
    border: none;
  }

  @media (min-width: @desktop-width) {
    padding-top: 18px;
    padding-bottom: 28px;
  }
}
